.cart {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  font-size: 18px;
  padding: 12px;
  background-color: $orange;
  border-radius: 25px;
  margin: 0px 0px 0px 15px;
  @media (max-width: 600px) {
    padding: 7px;
    font-size: 14px;
    margin: 0px 0px 0px 10px;
  }
  @media (max-width: 450px) {
    padding: 5px;
    font-size: 12px;
    margin: 0px 0px 0px 5px
  }

  svg {
    margin: 0px 0px 0px 5px;
    font-size: 25px;
    @media (max-width: 600px) {
      font-size: 18px;
    }
  }


  &__price {
    display: flex;
    align-items: center;

    &:hover > svg {
      transform: rotate(10deg);
    }


    &:after {
      content: "";
      color: white;
      display: block;
      width: 2px;
      height: 30px;
      border-right: 1px solid white;
      margin: 0px 10px;

      @media (max-width: 450px) {
        margin: 0px 5px;
      }
    }
  }

  &__qty {
    display: flex;
    align-items: center;

    &:hover > svg {
      transform: rotate(10deg);
    }
  }

}