@import '../variables';

.button {
  border: 1px solid $orange;
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 18px;
  padding: 10px 12px;
  border-radius: 25px;
  @media (max-width: 550px) {
    font-size: 12px;
    padding: 10px 5px;
  }

  span {
    color: $orange;
    margin: 0px 5px 0px 0px;
  }

  &:hover {
    transition: all 0.3s ease 0s;
    background-color: $orange;

    span {
      color: white
    }
  }
}
