@import './variables.scss';
@import './libs/normalize.scss';
@import './components/all.scss';

body {
  background-color: $wpapperColor;
}

.wrapper {
  height: 100%;
  @media (min-width: 768px) {
    margin: 25px auto;
  }
  @media (max-width: 767px) {
    padding: 25px 0px;
  }
  border-radius: 10px;
  max-width: 1400px;
}

.container {
  margin: 0px 15px;
  background-color: #fff;
  padding: 0px 50px;
}

.title {
  padding: 15px 0px 25px 0px;
}

.filter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 0px;
  @media (max-width: 1200px) {
    flex-direction: column;
  }
  @media (max-width: 767px) {
    padding: 10px 0px;
  }
}

.content {
  @media (max-width: 767px) {
    padding: 10px;
  }
  @media (max-width: 450px) {
    padding: 5px;
  }
}