.header {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-height: 110px;
  border-bottom: 1px solid $grey;
  padding: 30px 50px;
  margin: 0px 15px;
  background-color: white;

  &__logo {
    display: flex;
    align-items: center;
    @media (max-width: 1200px) {
      flex: 0 1 330px;
    }

    .header__title {
      h1 {
        font-weight: 800;
        @media (max-width: 600px) {
          font-size: 30px;
        }
        @media (max-width: 450px) {
          font-size: 18px;
        }
        @media (max-width: 390px) {
          font-size: 15px;
        }
      }

      p {
        color: grey;
        @media (max-width: 600px) {
          font-size: 10px;
        }
      }
    }
  }

  &__image {
    max-width: 50px;
    margin: 0px 10px 0px 0px;

    img {
      max-width: 100%;
    }

    @media (max-width: 450px) {
      max-width: 30px;
    }
  }
}