.cartPage {
  &__container {
    margin: 0 auto;
    max-width: 700px;
    padding: 50px 15px;
    @media (max-width: 550px) {
      padding: 5px 0px;
    }
  }

  &__header {
    display: flex;
    justify-content: space-between;

    svg {
      font-size: 25px;
      margin: 0px 15px 0px 0px;
      @media (max-width: 550px) {
        font-size: 15px;
      }
    }
  }

  &__clearCarts {
    cursor: pointer;
    display: flex;
    align-items: center;
    @media (max-width: 550px) {
      font-size: 14px;
    }

    &:hover {
      text-decoration: underline;
    }

    &:hover svg {
      color: $orange;
      transform: rotate(5deg) scale(1.1);
    }

  }

  &__block-wrapper {
    padding: 0px 0px 10px 0px;
    @media (max-width: 550px) {
      padding: 0px 0px 15px 0px;
      margin: 15px 0px 0px 0px;
    }
  }

  &__block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid $grey;
    @media (max-width: 550px) {
      padding: 0px 0px 10px 0px;
    }
  }

  &__info {
    display: flex;
    align-items: center;
    flex: 0 1 350px;
    @media (max-width: 650px) {
      flex: 0 1 250px;
    }

    img {
      max-width: 100px;
      @media (max-width: 650px) {
        max-width: 40px;
      }
      @media (max-width: 550px) {
        max-width: 30px;
      }
    }
  }

  &__title-block {
    @media (max-width: 550px) {
      margin: 0px 5px;
    }

    h3 {
      @media (max-width: 550px) {
        font-size: 14px;
      }
      @media (max-width: 390px) {
        font-size: 11px;
      }
    }

    p {
      @media (max-width: 550px) {
        font-size: 14px;
      }
      @media (max-width: 690px) {
        font-size: 11px;
      }
    }
  }

  &__quantity {
    display: flex;
    align-items: center;

    svg {
      cursor: pointer;
      font-size: 20px;

      &:hover {
        transform: scale(1.1);
        color: white;
        border-radius: 50%;
        overflow: hidden;
        background-color: $orange;
      }

      @media (max-width: 550px) {
        font-size: 15px;
      }
      @media (max-width: 390px) {
        font-size: 12px;
      }
    }

    span {
      font-size: 20px;
      margin: 0px 5px;
      @media (max-width: 550px) {
        font-size: 15px;
      }
      @media (max-width: 390px) {
        margin: 0px 3px;
        font-size: 11px;
      }
    }
  }

  &__price {
    display: flex;
    align-items: center;
    flex: 0 1 127px;

    svg {
      cursor: pointer;
      font-size: 20px;
      @media (max-width: 550px) {
        font-size: 16px;
      }

      &:hover {
        transform: scale(1.1);
        color: $orange;
      }
    }

    h3 {
      font-size: 20px;
      margin: 0px 20px 0px 0px;
      @media (max-width: 550px) {
        font-size: 10px;
        margin: 0 auto;
        padding: 0px 5px 0px 5px;
      }
    }
  }

  &__total-price {
    margin: 15px 0px;
    padding: 0px 15px;
    display: flex;
    justify-content: space-between;
    @media (max-width: 550px) {
      flex-direction: column;
      padding: 0;
    }

    h3 {
      @media (max-width: 550px) {
        font-size: 15px;
      }
    }
  }

  &__footer {
    display: flex;
    justify-content: space-between;
  }

  &__button-back {
    span {
      display: flex;
      align-items: center;

      svg {
        margin: 0px 5px 0px 0px;
      }
    }
  }

  &__disabled {
    pointer-events: none;
  }
}