.sort {
  display: inline-flex;
  position: relative;
  max-width: 300px;
  @media (max-width: 1200px) {
    align-self: flex-start;
  }
  @media (max-width: 450px) {
    font-size: 12px;
  }

  b {
    text-decoration: underline;
    color: $orange;
    overflow: hidden;
  }

  &__title {
    width: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: flex-end;
    cursor: pointer;

    span {
      display: inline-flex;
      align-items: center;
    }
  }

  svg {
    font-size: 20px;

    &.active {
      transition: all 0.3s ease 0s;
      transform: rotate(180deg);
    }
  }

  &__popup {
    position: absolute;
    visibility: hidden;
    top: 25px;
    right: -9%;

    &.active {
      transition: visibility 1s ease 0s;
      visibility: visible;
    }

    ul {
      background-color: #fbfcfc;
      padding: 10px;
      border-radius: 5px;

      li {
        padding: 15px 20px;
        border-radius: 5px;
        cursor: pointer;

        &.active {
          transition: all 0.3s ease 0s;
          background-color: #fff6f4;
          color: $orange;
        }
      }
    }
  }
}
