.search-block {
  margin-left: auto;
  display: flex;
  align-items: center;

  &__search-icon {
    display: flex;
    align-items: center;
    @media (min-width: 996px) {
      overflow: hidden;
      visibility: hidden;
    }
    @media (max-width: 995px) {
      font-size: 25px;
      position: relative;
      color: $grey;
      margin-left: auto;
    }
    @media (max-width: 450px) {
      font-size: 20px;
    }
  }
}

.search {
  position: relative;

  @media (max-width: 995px) {
    position: absolute;
    bottom: 150%;
    &.active {
      width: 100%;
      position: absolute;
      bottom: -37%;
      right: 0;
      opacity: 0.9;
      transition: all 1s ease 0s;
      @media (max-width: 767px) {
        bottom: -37%;
        margin: 0;
      }
      @media (max-width: 600px) {
        bottom: -59%;
      }
      @media (max-width: 450px) {
        bottom: -80%;
      }
    }
  }

  &__input {
    display: flex;
    align-items: center;
    border: 1px solid $grey;
    width: 100%;
    padding: 10px 5px;
    font-size: 22px;
    color: black;
    @media (max-width: 450px) {
      padding: 5px;
    }

    &:focus {
      border: 1px solid #979797;
      transition: all 0.3s ease 0s;
      transform: scale(1.02);
      transition: all 0.3s ease 0s;
      border: 1px solid #979797;
    }
  }

  &__icon {
    svg {
      font-size: 25px;
      position: absolute;
      top: 28%;
      right: 2%;
      color: $grey;
      cursor: pointer;
    }
  }

  &__delete {
    font-size: 25px;
    position: absolute;
    top: 28%;
    right: 13%;
    color: $grey;
    cursor: pointer;
    overflow: hidden;
    visibility: hidden;
    transition: all 0.3s ease 0s;

    &.active {
      transition: all 0.3s ease 0s;
      overflow: visible;
      visibility: visible;

      &:hover {
        opacity: 0.6;
        transform: scale(1.1);
        color: $orange;
      }
    }

    @media (max-width: 995px) {
      transform: translateX(-10px);
      right: 6%;
    }
  }
}