.pizzas {
  &__wrapper {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    width: 100%;
    @media (max-width: 1397px) {
      justify-content: space-between;
    }
    @media (max-width: 1080px) {
      justify-content: space-around;
    }
    @media (max-width: 683px) {
      justify-content: center;
    }
    @media (max-width: 650px) {
      justify-content: center;
    }
  }


  &__item {
    display: flex;
    max-width: 300px;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin: 0px 8px 30px 9px;

    h2 {
      min-height: 80px;
      padding: 10px 0px;
      text-align: center;
    }

    img {
      max-width: 100%;
    }
  }

  h2 {
    margin: 0px 0px 20px 0px;
    font-weight: 800;
    @media (max-width: 450px) {
      font-size: 20px;
    }
  }

  &__type-block {
    border-radius: 10px;
    background-color: #f3f3f3;
    width: 100%;
  }

  &__type {
    display: flex;
    justify-content: space-between;
    padding: 10px 5px 5px 5px;

    li {
      cursor: pointer;
      padding: 5px 0px;
      margin: 0px 5px;
      background-color: white;
      border-radius: 10px;
      text-align: center;
      flex: 1 1 50%;

      &.activeType {
        background-color: #dadada;
      }
    }
  }

  &__size {
    display: flex;
    justify-content: space-between;
    padding: 5px 5px 10px 5px;

    li {
      cursor: pointer;
      padding: 5px 0px;
      margin: 0px 5px;
      background-color: white;
      border-radius: 10px;
      text-align: center;
      flex: 1 1 33.333%;

      &.activeSize {
        background-color: #dadada;
      }
    }
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 10px;
  }

  &__price {
    font-weight: 800;
    padding: 5px;
  }

  &__button {
    svg {
      color: $orange;
    }

    span {
      b {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        background-color: $orange;
        color: white;
      }
    }

    &:hover {
      svg {
        color: white;
      }

      span {
        b {
          border: 1px solid white;
          color: $orange;
          background-color: white;
        }
      }
    }
  }
}


