.notFound {

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 0px;
    @media (max-width: 410px) {
      padding: 30px 0px;
    }

    h2 {
      margin: 0px 0px 20px 0px;
    }

    p {
      text-align: center;
      margin: 0px 0px 10px 0px;
      font-size: 20px;
      @media (max-width: 410px) {
        margin: 0px 0px 5px 0px;
        font-size: 15px;
      }
    }


    & > svg {
      color: red;
      font-size: 100px;
    }
  }

  &__button {
    cursor: pointer;
    background-color: black;
    padding: 10px 20px;
    border-radius: 20px;
    margin: 20px 0px 0px 0px;

    span {
      color: white;
    }

    &:hover {
      transition: all 0.3s ease 0s;
      transform: scale(1.02);
    }
  }
}
